/* Hover gradient card effect with white gradient and subtle lift */
.hover-gradient-card {
  position: relative;
  overflow: hidden;
  transition: transform 220ms ease, box-shadow 220ms ease, background-color 220ms ease;
}

.hover-gradient-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.25);
}

/* White gradient glow that fades in on hover */
.hover-gradient-card::after {
  content: "";
  position: absolute;
  inset: -1px; /* cover borders too */
  background: radial-gradient(80% 80% at 50% 0%, rgba(255, 255, 255, 0.18), rgba(255, 255, 255, 0) 70%),
              linear-gradient(180deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0));
  opacity: 0;
  transition: opacity 240ms ease;
  pointer-events: none;
}

.hover-gradient-card:hover::after {
  opacity: 1;
}

/* Squares canvas base sizing */
.squares-canvas {
  width: 100%;
  height: 100%;
  border: none;
  display: block;
}

/* Hide global Threads background for contact page only */
#global-threads { display: none !important; }

/* ===== Contact page readability overrides ===== */
.contact-page {
  position: relative;
}

/* Make cards solid and increase contrast */
.contact-page .hover-gradient-card {
  /* more translucent to blend with background */
  background-color: rgba(15, 23, 42, 0.60); /* slate-900 ~60% */
  backdrop-filter: blur(8px) saturate(120%);
  -webkit-backdrop-filter: blur(8px) saturate(120%);
  border: 1px solid rgba(255, 255, 255, 0.14);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.28);
}
.contact-page .hover-gradient-card::after {
  /* subtle sheen for depth */
  opacity: 0.35;
}
.contact-page .hover-gradient-card:hover::after {
  opacity: 0.55;
}

/* Text contrast */
.contact-page h1,
.contact-page h2,
.contact-page h3,
.contact-page p,
.contact-page label { color: rgba(255, 255, 255, 0.95); }

/* Inputs and textarea solid backgrounds */
.contact-page input,
.contact-page textarea {
  background-color: rgba(11, 18, 32, 0.55) !important; /* more translucent */
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.18) !important;
  color: #ffffff !important;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.03);
}
.contact-page input::placeholder,
.contact-page textarea::placeholder { color: rgba(255, 255, 255, 0.6); }

.contact-page input:focus,
.contact-page textarea:focus {
  outline: none;
  border-color: rgba(34, 211, 238, 0.7) !important; /* cyan-400 */
  box-shadow: 0 0 0 2px rgba(34, 211, 238, 0.35);
}

/* Submit button stronger fill */
.contact-page button[type="submit"] {
  background-color: #0891b2 !important; /* cyan-700 */
  border: 1px solid rgba(34, 211, 238, 0.4);
}
.contact-page button[type="submit"]:hover {
  background-color: #06b6d4 !important; /* cyan-600 */
}
